.table {
  margin-bottom: 15px;
}

.table__header {
  box-sizing: border-box;
  position: sticky;
  top: 0;
  background: #fff;
  white-space: pre;
  z-index: 2;
}

.table_type_fixed,
.table_type_scroll
{
  background: #f8f8f8;
}

.table__col {
  position: sticky;
}

.table__col_pos_first {
  left: 0;
  background: #fff;
}

.table__row {
  /*position: sticky;*/
  background-color: #00b0ff;
  z-index: 2;
}

.table__row_type_index {
  left: 0;
}

.table__row_type_even {
  background-color: #fff;
}

.table__row_type_odd {
  background-color: #f8f8f8;
}

.table__img {
  width: 48px;
  height: 48px;
  object-fit: cover;
}

.divvv {
  display: flex;
  /*position: sticky;*/
  /*top: 0;*/
  /*left: 0;*/
}

.table__left-col {
  position: sticky;
  left: 0;
}

