.table__cell_type_id {
  display: flex;
  cursor: pointer;
}

.table__cell_type_email {
  width: 250px;
}

.table__cell_type_phone {
  white-space: pre;
  width: 120px;
}

.table__cell_type_ip {
  width: 150px;
}

.table__cell_type_gender {
  width: 100px;
}

.table__cell_type_price {
  width: 85px;
}

.table__cell_type_img {
  width: 85px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.table_type_fixed {
  width: 350px;
  position: sticky;
  left: 0;
  z-index: 5;
  white-space: nowrap;
}

.table_type_scroll {
  /*position: sticky;*/
  /*top: 0;*/
  white-space: nowrap;
}

.table__icon {
  font-size: 20px;
}